<template>
  <div
    :class="
      'wssearch--filter wssearch--filter__' +
      name.toLowerCase().replace(/[^a-z0-9]/gim, '_')
    "
  >
    <span class="wssearch--filter-header" v-if="strippedBuckets.length">
      <label>
        {{ cleanName }}
      </label>
    </span>
    <div class="wssearch--filter-options">
      <facet-checbox
        v-for="(agg, i) in bucketsToShow"
        :agg="agg"
        :key="i + agg.key + name"
        :index="i"
        :name="name"
         v-show="i < collapsed || open"
      >
      </facet-checbox>
      <label
        class="wssearch--filter-showmore"
        v-if="strippedBuckets.length > collapsed"
        @click="open = !open"
      >
        <span>
          {{ $i18n("wssearch-" + lessOrMore) }}
        </span>
        <span
          class="wssearch--filter-showmore-icon"
          :class="'wssearch--filter-icon__' + lessOrMore"
        >
        </span>
      </label>
    </div>
  </div>
</template>

<script>
import FacetCheckbox from "./FacetCheckbox";
import Vue from "vue";

export default {
  components: {
    "facet-checbox": FacetCheckbox,
  },
  props: {
    name: String,
    label: String,
    buckets: [Array, Object],
    translation: String,
    query: String,
    type: String,
  },
  data() {
    return {
      open: false,
      // eslint-disable-next-line no-undef
      facetSize: mw.config.values.WSSearchFront.config.settings.facets,
      translations: "",
      strippedBuckets: "",
      bucketsToShow: "",
      fired: false,
    };
  },
  watch: {
    propChange() {
      this.organize();
    }
  },
  mounted() {
    if (this.translation) {
      var that = this;
      var params = {
          action: "ask",
          query: this.query + "|limit=500|?" + this.translation,
          format: "json",
          formatversion: 2,
        },
        // eslint-disable-next-line no-undef
        api = new mw.Api();

      api.post(params).done(function (data) {
        if (data.query) {
          that.translations = data.query.results;
          that.organize();
        }
      });
    } else {
      this.organize();
    }
  },
  methods: {
    organize() {
      var that = this;
   
      var or = "";
      // check if its a array, if not create array from object
      if (Array.isArray(this.buckets)) {
        or = this.buckets;
      } else {
        var keys = [];

        for (var number in this.buckets) {
          var as = this.buckets[number];
          as.key = number;
          keys.push(as);
        }
        or = keys;
      }

      var trans = that.translations;
      var clean = or.filter((el) => el.doc_count > 0);

      if (this.translation) {
        clean.forEach((element, i) => {
          if (
            trans[element.key] &&
            trans[element.key].printouts[that.translation]
          ) {
            if (trans[element.key].printouts[that.translation][0].fulltext) {
              clean[i].name =
                trans[element.key].printouts[that.translation][0].fulltext;
            } else {
              clean[i].name = trans[element.key].printouts[that.translation][0];
            }
          }
        });

        var selected = this.$store.state.selected;

        if (selected.length > 0 && !this.fired) {
          selected.forEach((element, i) => {
            if (
              trans[element.value] &&
              trans[element.value].printouts[that.translation]
            ) {
              if (
                trans[element.value].printouts[that.translation][0].fulltext
              ) {
                selected[i].name =
                  trans[element.value].printouts[that.translation][0].fulltext;
                Vue.set(this.$store.state.selected, i, selected[i]);
              } else {
                selected[i].name =
                  trans[element.value].printouts[that.translation][0];
                Vue.set(this.$store.state.selected, i, selected[i]);
              }
            }
          });

          this.fired = true;
        } else {
          this.fired = true;
        }
      }

      if (that.type == "date") {
        this.strippedBuckets = clean.reverse();
      } else if (
        // eslint-disable-next-line no-undef
        mw.config.values.WSSearchFront.config.facetSettings[that.name] &&
        // eslint-disable-next-line no-undef
        mw.config.values.WSSearchFront.config.facetSettings[that.name].sort ==
          "alphabetically"
      ) {
        this.strippedBuckets = clean.sort(function (a, b) {
          var textA = a.key.toUpperCase();
          var textB = b.key.toUpperCase();
          return textA < textB ? -1 : textA > textB ? 1 : 0;
        });
      } else {
        this.strippedBuckets = clean;
      }

      this.bucketsToShow =  this.strippedBuckets;
    }
  },
  computed: {
    collapsed() {
      return this.facetSize ? parseInt(this.facetSize) : 5;
    },
    propChange() {
      return this.buckets;
    },
    cleanName() {
      if (this.label) {
        return this.label;
      }
      return this.name.replace(/_/g, " ");
    },
    lessOrMore() {
      return this.open ? "less" : "more";
    }
  }
};
</script>

<style >
.wssearch--filter-showmore {
  display: block;
  width: 100%;
  height: auto;
  cursor: pointer;
  background-color: var(--tint-1);
  border: var(--border-1);
  -webkit-transition: background-color 100ms, color 100ms, border-color 100ms,
    box-shadow 100ms;
  -moz-transition: background-color 100ms, color 100ms, border-color 100ms,
    box-shadow 100ms;
  transition: background-color 100ms, color 100ms, border-color 100ms,
    box-shadow 100ms;
  display: inline-flex;
  justify-content: center;
  border-radius: 2px;
  font-weight: bold;
}
.wssearch--filter-showmore:hover {
  border-color: var(--base-color);
  background-color: white;
  color: var(--base-color);
}
.wssearch--filter-showmore:focus,
.wssearch--filter-showmore:active {
  border-color: var(--base-color);
  box-shadow: inset 0 0 0 1px var(--base-color), inset 0 0 0 2px #fff;
  outline: 1px solid transparent;
}

.wssearch--filter-showmore-icon {
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
  display: inline-block;
  width: 1em;
  margin-left: 1em;
}
.wssearch--filter-icon__more {
  background-image: var(--down-icon);
}
.wssearch--filter-icon__less {
  background-image: var(--up-icon);
}
</style>
