<template>
  <div
    v-if="buckets.length"
    :class="
      'wssearch--filter wssearch--filter__' +
      name.toLowerCase().replace(/[^a-z0-9]/gim, '_')
    "
  >
    <span class="wssearch--filter-header">
      <label>
        {{ filterName }}
      </label>
    </span>
    <div class="wssearch--selected-filters__combobox">
      <pill-item
        v-for="(activefilter, index) in pils"
        :key="index"
        :activefilter="activefilter"
        :label="activefilter.name"
      ></pill-item>
    </div>
    <div class="wssearch--filter-options-search-wrapper">
      <span
        class="wssearch--search-clear wssearch--search-clear__combobox"
        v-if="term"
        tabindex="-1"
        aria-label="Clear"
        role="button"
        @click="clearInput"
      >
      </span>
      <input
        type="input"
        :value="term"
        :placeholder="filterName"
        class="wssearch--filter-options-search"
        @input="searchAggs"
        @keyup.enter="searchForAsk"
      />

      <div class="wssearch--filter-options wssearch--filter-options__combobox">
        <facet-checkbox
          v-for="(agg, i) in sortedBuckets"
          :agg="agg"
          :key="i + agg.key + name"
          :index="i"
          :name="name"
        ></facet-checkbox>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";

import PillItem from "./PillItem.vue";
import FacetCheckbox from "./FacetCheckbox.vue";

export default {
  components: {
    "pill-item": PillItem,
    "facet-checkbox": FacetCheckbox,
  },
  props: {
    name: String,
    label: String,
    buckets: [Array, Object],
  },
  methods: {
    searchForAsk: function (e) {
      this.term = e.target.value;
      this.$emit("search", this.term);
    },
    searchAggs: function (e) {
      this.term = e.target.value;
      this.$emit("input", this.term);
      var re = new RegExp(this.term, "ig");
      var bucks = this.buckets;
      bucks.forEach(function (agg) {
        if (!agg.key.match(re)) {
          Vue.set(agg, "show", "no");
        } else {
          Vue.set(agg, "show", "yes");
        }
      });
    },
    clearInput() {
      this.term = "";
    },
  },
  data() {
    return {
      term: "",
    };
  },
  computed: {
    filterName() {
      if (this.label) {
        return this.label;
      }
      return this.name.replace("_", " ");
    },
    sortedBuckets() {
      if (
        // eslint-disable-next-line no-undef
        mw.config.values.WSSearchFront.config.facetSettings[this.name] &&
        // eslint-disable-next-line no-undef
        mw.config.values.WSSearchFront.config.facetSettings[this.name].sort ==
          "alphabetically"
      ) {
        var unsorted = this.buckets;
        var sorted = unsorted.sort(function (a, b) {
          var textA = a.key.toUpperCase();
          var textB = b.key.toUpperCase();
          return textA < textB ? -1 : textA > textB ? 1 : 0;
        });
        return sorted;
      } else {
        return this.buckets;
      }
    },
    pils() {
      var root = this;
      var selected = this.$store.state.selected;
      var pilss = selected.filter(function (el) {
        if (el.key == root.name) {
          return el;
        }
      });
      return pilss;
    },
  },
};
</script>

