<template>
  <facet-combobox
    :buckets="buckets"
    :name="name"
    :label="label"
    @input="ask"
    @search="search"
  ></facet-combobox>
</template>

<script>
import Vue from "vue";
import FacetCombobox from "./FacetCombobox.vue";

export default {
  name: "FacetAskCombobox",
  components: {
    "facet-combobox": FacetCombobox,
  },
  props: {
    name: String,
    label: String,
    query: String,
    qdata: String,
    qtext: String,
  },
  data() {
    return {
      buckets: [{ doc_count: 1, key: "", show: "no" }],
    };
  },
  mounted() {
    var that = this;
    var sel = that.$store.state.selected;
    sel.forEach((el) => {
      if (el.key && el.key == that.name && el.type != "query") {
        that.ask(el.value, "inital");
      }
    });
  },
  methods: {
    search(term) {
      if (
        // eslint-disable-next-line no-undef
        mw.config.values.WSSearchFront.config.facetSettings[this.name] &&
        // eslint-disable-next-line no-undef
        mw.config.values.WSSearchFront.config.facetSettings[this.name].search
      ) {
        var that = this;
        var selection = this.$store.state.selected;
        var newSelection = selection.filter(function (selected) {
          if (selected.key != that.name) {
            return selected;
          }
        });
        if (term) {
          that.buckets.push({
            key: term,
            doc_count: 1,
            type: "query",
            show: "no",
          });
          newSelection.push({ key: this.name, value: term, type: "query" });
        }
        this.$store.commit("SET_SELECTED", newSelection);
      }
    },
    ask(term, inital) {
      var outputData = this.qdata;
      var outputLabel = this.qtext;
      var that = this;
      var askQuery = "";
      if (outputLabel) {
        askQuery =
          this.query +
          "[[" +
          outputLabel +
          "::in:" +
          term +
          "]] |?" +
          outputData +
          "|?" +
          outputLabel;
      } else {
        askQuery = this.query + "[[" + outputData + "::in:" + term + "]] |?" + outputData;
      }
      if (inital) {
        askQuery =
          this.query +
          "[[" +
          outputData +
          "::in:" +
          term +
          "]] |?" +
          outputData +
          "|?" +
          outputLabel;
      }
      var params = {
          action: "ask",
          query: askQuery,
          format: "json",
          formatversion: 2,
        },
        // eslint-disable-next-line no-undef
        api = new mw.Api();
      api.post(params).done(function (data) {
        var alreadySelected = [];
        var selected = that.$store.state.selected;
        var outputBuckets = selected
          .map(function (el) {
            if (el.key && el.key == that.name) {
              alreadySelected.push(el.value);
              var outputSelected = { key: el.value, doc_count: 1 };
              if (el.name) {
                outputSelected.name = el.name;
              }
              if (el.type == "query") {
                outputSelected.type = "query";
                outputSelected.show = "no";
              }
              return outputSelected;
            }
          })
          .filter((x) => x);
        for (const [key, value] of Object.entries(data.query.results)) {
          var outkey = value.printouts[outputData];
          if (outkey) {
            outkey = outkey[0].fulltext ? outkey[0].fulltext : outkey[0];
            var buck = {
              doc_count: 1,
              key: outkey,
              page: key,
            };
            if (outputLabel && value.printouts[outputLabel]) {
              var label = value.printouts[outputLabel];
              buck.name = label[0].fulltext ? label[0].fulltext : label[0];
              selected.forEach((el, i) => {
                if (
                  el.key &&
                  el.key == that.name &&
                  el.value &&
                  el.value == outkey
                ) {
                  selected[i].name = buck.name;
                  if (el.type == "query") {
                    selected[i].type = "query";
                  }
                  Vue.set(that.$store.state.selected, i, selected[i]);
                  outputBuckets[i].name = buck.name;
                }
              });
            }
            if (!alreadySelected.includes(outkey)) {
              outputBuckets.push(buck);
            }
          }
        }
        outputBuckets.length > 0
          ? (that.buckets = outputBuckets)
          : (that.buckets = [{ doc_count: 1, key: "", show: "no" }]);
      });
    },
  },
};
</script>