<template>
  <label
    v-show="show"
    class="wssearch--checkbox"
    :class="labelClass"
    aria-disabled="false"
  >
    <span class="wssearch--checkbox-input" aria-disabled="false">
      <input
        class="wssearch--checkbox-input-checkbox"
        type="checkbox"
        :id="createID"
        v-model="modelSelected"
        :value="val"
        aria-disabled="false"
        tabindex="0"
      />
      <span class="wssearch--checkbox-input-icon" aria-disabled="false"> </span>
    </span>
    <span>
      <span class="wssearch--checkbox-title">
        {{ title }}
      </span>
      <span class="wssearch--checkbox-count" title="count">
        {{ agg.doc_count }}
      </span>
    </span>
  </label>
</template>

<script>
import Vue from "vue";

export default {
  name: "facet-checkbox",
  props: {
    agg: Object,
    name: String,
    index: Number,
  },
  mounted() {
    var selected = this.$store.state.selected;
    if (selected.length > 0 && this.agg.key_as_string) {
      var that = this;
      selected.forEach((element, i) => {
        if (element.key == that.name) {
          // eslint-disable-next-line no-undef
          selected[i].name = mw
            .message("search-" + that.agg.key_as_string)
            .text();
          Vue.set(this.$store.state.selected, i, selected[i]);
        }
      });
    }
  },
  computed: {
    modelSelected: {
      set(val) {
        this.$store.commit("SET_SELECTED", val);
      },
      get() {
        return this.$store.state.selected;
      },
    },
    labelClass() {
      var selectedClass = "";
      var that = this;
      var selected = this.$store.state.selected;
      var matches = selected.filter(function (el) {
        if (
          el.value == that.agg.key ||
          (that.agg.key_as_string && el.value == that.agg.key_as_string)
        ) {
          return el;
        }
      });
      if (matches.length) {
        selectedClass = " wssearch--checkbox-selected";
      }
      var sk = this.agg.key_as_string ? this.agg.key_as_string : this.agg.key;
      if (!sk) {
         sk = "_"
      }
      return (
        "wssearch--checkbox__" +
        this.name.toLowerCase().replace(/[^a-z0-9]/gim, "_") +
        "--" +
        sk.toLowerCase().replace(/[^a-z0-9]/gim, "_") +
        selectedClass
      );
    },
    title() {
      if (this.agg.name) {
        return this.agg.name;
      } else {
        return this.agg.key_as_string
          // eslint-disable-next-line no-undef
          ? mw.message("search-" + this.agg.key_as_string).text()
          : this.agg.key;
      }
    },
    createID() {
      var sk = this.agg.key_as_string ? this.agg.key_as_string : this.agg.key;
      if (!sk) {
         sk = "_"
      }
      return (
        this.name.toLowerCase().replace(/[^a-z0-9]/gim, "_") +
        "--" +
        sk.toLowerCase().replace(/[^a-z0-9]/gim, "_")
      );
    },
    show() {
      if (this.agg.show == "no") {
        return false;
      } else {
        if (this.agg.doc_count > 0) {
          return true;
        }
      }
      return false;
    },
    val() {
      var sk = this.agg.key_as_string ? this.agg.key_as_string : this.agg.key;
      if (this.agg.to) {
        return {
          value: this.agg.key,
          key: this.name,
          range: {
            gte: Number(this.agg.from + ".0000000"),
            lte: Number(this.agg.to + ".0000000"),
          },
        };
      } else {
        var out = { value: sk, key: this.name };
        if (this.agg.name) {
          out.name = this.agg.name;
        }
        if (this.agg.key_as_string) {
          // eslint-disable-next-line no-undef
          out.name = mw.message("search-" + this.agg.key_as_string).text();
        }
        if (this.agg.type) {
          out.type = this.agg.type;
        }
        return out;
      }
    },
  },
};
</script>

<style>
.wssearch--checkbox {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-gap: 0.5em;
  align-items: center;
}

.wssearch--checkbox-count {
  margin-left: 0.5em;
  white-space: nowrap;
}

.wssearch--checkbox-count:after {
  content: ")";
}

.wssearch--checkbox-count:before {
  content: "(";
}

.wssearch--checkbox-input-checkbox:checked + .wssearch--checkbox-input-icon {
  background-color: var(--base-color);
  border-color: var(--base-color);
  background-size: 1em 1em;
}
.wssearch--checkbox-input-checkbox:hover + .wssearch--checkbox-input-icon {
  border-color: var(--base-color-lighter);
}
.wssearch--checkbox-input-checkbox:focus + .wssearch--checkbox-input-icon {
  border-color: var(--base-color);
  box-shadow: inset 0 0 0 1px var(--base-color);
  outline: 1px solid transparent;
}

.wssearch--checkbox-input-checkbox:checked:hover
  + .wssearch--checkbox-input-icon {
  background-color: var(--base-color-lighter);
  border-color: var(--base-color-lighter);
}

.wssearch--checkbox-input-checkbox:checked:focus
  + .wssearch--checkbox-input-icon {
  background-color: var(--base-color);
  border-color: var(--base-color);
  box-shadow: inset 0 0 0 1px var(--base-color), inset 0 0 0 2px #fff;
}

.wssearch--checkbox-input-icon {
  cursor: pointer;
  -webkit-transition: background-color 100ms, border-color 100ms,
    box-shadow 100ms;
  -moz-transition: background-color 100ms, border-color 100ms, box-shadow 100ms;
  transition: background-color 100ms, border-color 100ms, box-shadow 100ms;
  background-color: #fff;
  background-size: 0 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  position: absolute;
  left: 0;
  width: 1.42857143em;
  height: 1.42857143em;
  border-color: #72777d;
  border-style: solid;
  border-radius: 2px;
  border-width: 1px;
  background-position: center center;
  background-repeat: no-repeat;
}

.wssearch--checkbox-input {
  position: relative;
}

.wssearch--checkbox-input-icon {
  background-image: linear-gradient(transparent, transparent), var(--check-icon);
}
</style>
