<template>
  <div class="wssearch--selected">
    <pill-item
      v-for="(activefilter, index) in selected"
      :key="index"
      :activefilter="activefilter"
      :label="activefilter.name"
    ></pill-item>
    <span
      class="wssearch--selected-clear"
      v-if="selected.length"
      @click="clearfilters"
      tabindex="-1"
      aria-label="Clear"
      role="button"
    >
      {{ $i18n("clear-all-filters") }}
    </span>
    <div style="display: none">
      <facet-checkbox
        v-for="(agg, i) in hiddenboxes"
        :agg="{ key: agg.value, doc_count: 1 }"
        :key="i"
        :index="i"
        :name="agg.key"
      ></facet-checkbox>
    </div>
  </div>
</template>

<script>
import PillItem from "./PillItem.vue";
import FacetCheckbox from "./FacetCheckbox";

export default {
  components: {
    "pill-item": PillItem,
    "facet-checkbox": FacetCheckbox,
  },
  computed: {
    selected() {
      return this.$store.state.selected;
    },
    hiddenboxes() {
      var boxes = [];

      this.selected.forEach((sel) => {
        if (
          !this.$store.state.aggs ||
          (this.$store.state.aggs[sel.key] &&
            this.$store.state.aggs[sel.key].doc_count < 1)
        ) {
          boxes.push(sel);
        }
      });

      return boxes;
    },
  },
  methods: {
    clearfilters() {
      // eslint-disable-next-line no-undef
      if (mw.config.values.WSSearchFront.config.settings.clear) {
        this.$store.commit("SET_TERM", "");
        this.$store.commit("SET_SELECTED", []);
      } else {
        this.$store.commit("SET_SELECTED", []);
      }
    },
  },
};
</script>

<style>
.wssearch--selected {
  grid-area: selected;
}
.wssearch--selected-clear {
  white-space: nowrap;
  cursor: pointer;
  color: var(--base-color);
  border: 1px solid transparent;
  padding: 6px 6px;
}
</style>
