<template>
  <div class="wssearch--order">
    <span class="wssearch--order__label">
      {{ $i18n("search-order") }}
    </span>
    <div class="wssearch--order__wrapper">
      <span class="wssearch--order__select" tabindex="0" aria-disabled="false">
        {{ sortordertype == "score" ? $i18n("search-score") : sortordertype }}
      </span>
      <div class="wssearch--order__options">
        <span
          class="wssearch--order__option"
          @click="$store.commit('SET_ORDERTYPE', 'score')"
        >
          {{ $i18n("search-score") }}
        </span>
        <span
          v-for="(order, key) in settings['sort options']"
          :key="key"
          @click="$store.commit('SET_ORDERTYPE', key)"
          class="wssearch--order__option"
        >
          {{ order.label ? order.label : key }}
        </span>
      </div>
    </div>
    <span class="wssearch--order__sortorder" @click="setorder">
      <span :class="'wssearch--order__icon wssearch--order__' + sortorder">
      </span>
    </span>
  </div>
</template>

<script>
export default {
  props: {
    settings: Object,
  },
  computed: {
    sortordertype() {
      if (
        this.settings["sort options"][this.$store.state.sortordertype] &&
        this.settings["sort options"][this.$store.state.sortordertype].label
      ) {
        return this.settings["sort options"][this.$store.state.sortordertype]
          .label;
      }
      return this.$store.state.sortordertype;
    },
    sortorder() {
      return this.$store.state.sortorder;
    },
  },
  methods: {
    setorder() {
      this.sortorder == "desc"
        ? this.$store.commit("SET_ORDER", "asc")
        : this.$store.commit("SET_ORDER", "desc");
    },
  },
};
</script>

