<template>
  <div>
    <div :class="'wssearch--filter'" v-for="(typer, i) in outbuks" :key="i">
      <span class="wssearch--filter-header" v-if="typer.buckets.length">
        <label>
          {{ i }}
        </label>
      </span>
      <div class="wssearch--filter-options">
        <template v-if="typer.buckets.length > collapsed && !open.includes(i)">
          <facet-checbox
            v-for="(agg, i) in typer.buckets.slice(0, collapsed)"
            :agg="agg"
            :key="i + agg.key + name"
            :index="i"
            :name="name"
          ></facet-checbox>
          <facet-checbox
            style="display: none"
            v-for="(agg, i) in typer.buckets.slice(
              collapsed,
              typer.buckets.length
            )"
            :agg="agg"
            :key="i + agg.key + name"
            :index="i"
            :name="name"
          ></facet-checbox>
        </template>
        <template v-else>
          <facet-checbox
            v-for="(agg, i) in typer.buckets"
            :agg="agg"
            :key="i + agg.key + name"
            :index="i"
            :name="name"
          ></facet-checbox>
        </template>
        <label
          class="wssearch--filter-showmore"
          v-if="typer.buckets.length > collapsed"
          @click="lessOrMore(i)"
        >
          <span>
            {{ $i18n(open.includes(i) ? "wssearch-less" : "wssearch-more") }}
          </span>
          <span
            class="wssearch--filter-showmore-icon"
            :class="
              open.includes(i)
                ? 'wssearch--filter-icon__less'
                : 'wssearch--filter-icon__more'
            "
          >
          </span>
        </label>
      </div>
    </div>
  </div>
</template>

<script>
import FacetCheckbox from "./FacetCheckbox";

export default {
  components: {
    "facet-checbox": FacetCheckbox,
  },
  props: {
    name: String,
    label: String,
    buckets: [Array, Object],
    qdata: String,
    query: String,
    type: String,
    qtext: String,
  },
  data() {
    return {
      open: [],
      // eslint-disable-next-line no-undef
      facetSize: mw.config.values.WSSearchFront.config.settings.facets,
      translations: "",
      strippedBuckets: "",
      bucketsToShow: "",
      fired: false,
    };
  },
  mounted() {
    var that = this;
    var params = {
        action: "ask",
        query: this.query + "|limit=500|?" + this.qdata + "|?" + this.qtext,
        format: "json",
        formatversion: 2,
      },
      // eslint-disable-next-line no-undef
      api = new mw.Api();

    api.post(params).done(function (data) {
      if (data.query) {
        var stripQueryData = that.qdata.split(".");
        var strip = stripQueryData[stripQueryData.length - 1];

        var newTranslations = {};
        for (var element in data.query.results) {
          var queryResult = data.query.results[element];
          if (
            queryResult.printouts &&
            queryResult.printouts[that.qtext] &&
            queryResult.printouts[strip]
          ) {
            var sr = "";
            var rs = "";
            if (
              queryResult.printouts[that.qtext][0] &&
              queryResult.printouts[that.qtext][0].fulltext
            ) {
              sr = queryResult.printouts[that.qtext][0].fulltext;
            } else {
              sr = queryResult.printouts[that.qtext][0];
            }

            if (queryResult.printouts[strip][0] && queryResult.printouts[strip][0].fulltext) {
              rs = queryResult.printouts[strip][0].fulltext;
            } else {
              rs = queryResult.printouts[strip][0];
            }
            newTranslations[sr] = rs;
          }
        }

        that.translations = newTranslations;
      }
    });
  },
  methods: {
    lessOrMore(ob) {
      var index = this.open.indexOf(ob);
      if (index > -1) {
        this.open.splice(index, 1);
      } else {
        this.open.push(ob);
      }
    },
  },
  computed: {
    collapsed() {
      return this.facetSize ? parseInt(this.facetSize) : 5;
    },
    outbuks() {
      var out = {};
      var newTranslations = this.translations;
      this.buckets.forEach((element) => {
        if (newTranslations[element.key]) {
          if (!out[newTranslations[element.key]]) {
            out[newTranslations[element.key]] = { buckets: [element] };
          } else {
            out[newTranslations[element.key]].buckets.push(element);
          }
        }
      });

      var sorted = Object.keys(out)
        .sort()
        .reduce(
          (acc, key) => ({
            ...acc,
            [key]: out[key],
          }),
          {}
        );

      return sorted;
    },
    cleanname() {
      if (this.label) {
        return this.label;
      }
      return this.name.replace(/_/g, " ");
    },
    lessormore() {
      return this.open ? "less" : "more";
    },
  },
};
</script>

<style >
.wssearch--filter-showmore {
  display: block;
  width: 100%;
  height: auto;
  cursor: pointer;
  background-color: var(--tint-1);
  border: var(--border-1);
  -webkit-transition: background-color 100ms, color 100ms, border-color 100ms,
    box-shadow 100ms;
  -moz-transition: background-color 100ms, color 100ms, border-color 100ms,
    box-shadow 100ms;
  transition: background-color 100ms, color 100ms, border-color 100ms,
    box-shadow 100ms;
  display: inline-flex;
  justify-content: center;
  border-radius: 2px;
  font-weight: bold;
}
.wssearch--filter-showmore:hover {
  border-color: var(--base-color);
  background-color: white;
  color: var(--base-color);
}
.wssearch--filter-showmore:focus,
.wssearch--filter-showmore:active {
  border-color: var(--base-color);
  box-shadow: inset 0 0 0 1px var(--base-color), inset 0 0 0 2px #fff;
  outline: 1px solid transparent;
}

.wssearch--filter-showmore-icon {
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
  display: inline-block;
  width: 1em;
  margin-left: 1em;
}
.wssearch--filter-icon__more {
  background-image: var(--down-icon);
}
.wssearch--filter-icon__less {
  background-image: var(--up-icon);
}
</style>
