import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

var routing = false;

var updateStore = function(store) {
  store.subscribe(function(mutation, state) {
    if (
      mutation.type === "START" ||
      mutation.type === "SET_TERM" ||
      mutation.type === "SET_FROM" ||
      mutation.type === "SET_ORDERTYPE" ||
      mutation.type === "SET_ORDER" ||
      mutation.type === "SET_SIZE" ||
      mutation.type === "SET_SELECTED"
    ) {
      if (mutation.type != "SET_FROM") {
        state.from = 0;
      }

      //mount function need to move this to somewhere else
      if (mutation.type === "START") {
        //create dates
        var today = new Date();
        var lastWeek = new Date();
        lastWeek.setDate(lastWeek.getDate() - 7);
        var lastMonth = new Date();
        lastMonth.setMonth(lastMonth.getMonth() - 1);
        var lastQuarter = new Date();
        lastQuarter.setMonth(lastQuarter.getMonth() - 4);

        var readableToday = readableDate(today);

        var date_ranges = [];
        var real_date_ranges = {};

        real_date_ranges["Last Week"] = {
          from: readableDate(lastWeek),
          to: readableToday,
        };
        real_date_ranges["Last Month"] = {
          from: readableDate(lastMonth),
          to: readableToday,
        };
        real_date_ranges["Last Quarter"] = {
          from: readableDate(lastQuarter),
          to: readableToday,
        };

        for (var i = 0; i < 5; i++) {
          var key = today.getFullYear() - i;
          real_date_ranges[key] = { from: key + "-1-1", to: key + 1 + "-1-1" };
        }

        Object.entries(real_date_ranges).forEach(function([key, value]) {
          date_ranges.push({
            key: key.toString(),
            from: createDates(value.from),
            to: createDates(value.to),
          });
        });

        var facetranges = [
          {
            type: "range",
            ranges: date_ranges,
            property: "Modification date",
          },
        ];

        state.realDates = real_date_ranges;

        //add more ranges

        var rangeprop = [];
        // eslint-disable-next-line no-undef
        var facetSettings =
          window.mw.config.values.WSSearchFront.config.facetSettings;
        Object.keys(facetSettings).forEach(function(key) {
          if (facetSettings[key].display == "range") {
            facetSettings[key].name = key;
            rangeprop.push(facetSettings[key]);
          }
        });

        if (rangeprop.length > 0) {
          rangeprop.forEach(function(prop) {
            var max = parseInt(prop.max);
            var step = parseInt(prop.step);

            var pessa = [];

            if (prop.type == "date") {
              for (var x = 0; x < max; x++) {
                var key = today.getFullYear() - x;
                state.realDates[key] = {
                  from: key + "-1-1",
                  to: key + 1 + "-1-1",
                };
                pessa.push({
                  key: key.toString(),
                  from: createDates(key + "-1-1"),
                  to: createDates(key + 1 + "-1-1"),
                });
              }
            } else {
              for (var i = 1; i < max + 1; i += step) {
                // pessa.push( { "from": i, "to": i + step, "key": (i + step) + '' } )
                pessa.push({ from: i, to: max + 1, key: i + step - 1 + "" });
              }
            }

            facetranges.push({
              type: "range",
              ranges: pessa,
              property: prop.name,
            });
          });
        }

        state.dates = facetranges;

        //get from url string

        var queryString = window.location.search;
        var urlParams = new URLSearchParams(queryString);
        var page = urlParams.get("title");
        if (page) {
          routing = page;
        }

        var term = urlParams.get("term");
        var offset = urlParams.get("offset");
        var filters = urlParams.get("filters");
        var ortype = urlParams.get("ordertype");
        var or = urlParams.get("order");

        if (term) {
          state.term = term;
        }

        if (offset) {
          state.from = parseInt(offset);
        }

        if (or) {
          state.sortorder = or;
        }

        if (ortype) {
          state.sortordertype = ortype;
        }

        if (filters) {
          var urlFiltersOutput = [];
          var activeFilters = filters.split("~~");

          activeFilters.forEach(function(value) {
            var filterItem = value.split("^^");
            var rangeItem = filterItem[0].split("_");

            if (rangeItem[0] == "range") {
              var ranges = filterItem[1].split("_");

              if (rangeItem[1] == "customrange") {
                Vue.set(state.realDates, "customrange", {
                  from: ranges[0],
                  to: ranges[1],
                });
              }

              if (
                rangeItem[1] == "customrange" ||
                rangeItem[1] == "date" ||
                rangeItem[2] == "Modification date"
              ) {
                state.rangeFrom = createDates(ranges[0]);
                state.rangeTo = createDates(ranges[1]);

                urlFiltersOutput.push({
                  key: rangeItem[2],
                  value: rangeItem[1],
                  range: {
                    gte: state.rangeFrom,
                    lte: state.rangeTo,
                  },
                });
              } else {
                if (facetSettings[rangeItem[2]].type != "date") {
                  urlFiltersOutput.push({
                    key: rangeItem[2],
                    value: rangeItem[1],
                    range: {
                      gte: ranges[0],
                      lte: ranges[1],
                    },
                  });
                } else {
                  urlFiltersOutput.push({
                    key: rangeItem[2],
                    value: rangeItem[1],
                    range: {
                      gte: createDates(ranges[0]),
                      lte: createDates(ranges[1]),
                    },
                  });
                }
              }
            } else {
              if (rangeItem[0] == "query") {
                urlFiltersOutput.push({
                  value: filterItem[1],
                  key: rangeItem[1],
                  type: "query",
                });
              } else {
                urlFiltersOutput.push({
                  value: filterItem[1],
                  key: filterItem[0],
                });
              }
            }
          });
          state.selected = urlFiltersOutput;
        }
      }

      //api call, this can stay here

      var root = state;
      root.loading = true;

      var urlsytr = urlstring();
      if (routing) {
        routing = false;
      }

      window.history.replaceState("", "", urlsytr);

      var orobj = {};
      var selected = [];
      root.selected.forEach((element) => {
        if (
          // eslint-disable-next-line no-undef
          mw.config.values.WSSearchFront.config.facetSettings[element.key] &&
          // eslint-disable-next-line no-undef
          mw.config.values.WSSearchFront.config.facetSettings[element.key].logic &&
          // eslint-disable-next-line no-undef
          mw.config.values.WSSearchFront.config.facetSettings[element.key].logic == "or"
        ) {

          if (!orobj[element.key]) {
            orobj[element.key] = [element.value];
          } else {
            orobj[element.key].push(element.value);
          }
        } else {
          selected.push(element);
        }
      });

      Object.keys(orobj).forEach(function(key) {
        selected.push({ key: key, value: orobj[key] });
      });

      var params = {
        action: "query",
        meta: "WSSearch",
        format: "json",
        filter: JSON.stringify(selected),
        term: root.term,
        from: root.from,
        limit: root.size,
        // eslint-disable-next-line no-undef
        pageid: mw.config.values.wgArticleId,
        aggregations: JSON.stringify(root.dates),
      };
     
      if (
         // eslint-disable-next-line no-undef
        mw.config.values.WSSearchFront.config.settings["sort options"] &&
        root.sortordertype != "score"
      ) {
        params.sortings = JSON.stringify([
          {
            type: "property",
            property: root.sortordertype,
            order: root.sortorder,
          },
        ]);
      }
      // eslint-disable-next-line no-undef
      if (mw.config.values.WSSearchFront.config.settings["sort"]) {
        params.sortings = JSON.stringify([
          {
            type: "property",
            // eslint-disable-next-line no-undef
            property: mw.config.values.WSSearchFront.config.settings["sort"],
            order: "asc",
          },
        ]);
      }
      // eslint-disable-next-line no-undef
      var api = new mw.Api();

      api.post(params).done(function(data) {
        root.total = data.result.total;
        root.hits = JSON.parse(data.result.hits);
        root.aggs = data.result.aggs;
        root.loading = false;
      });
    }

    function urlstring() {
      //create url string

      var url = new URL(window.location.href);
      var searchParams = url.searchParams;

      state.from > 0
        ? searchParams.set("offset", state.from)
        : searchParams.delete("offset");

      state.term
        ? searchParams.set("term", state.term)
        : searchParams.delete("term");

      // eslint-disable-next-line no-undef
      if (mw.config.values.WSSearchFront.config.settings["sort options"]) {
        state.sortorder && state.sortordertype != "score"
          ? searchParams.set("order", state.sortorder)
          : searchParams.delete("order");

        state.sortordertype && state.sortordertype != "score"
          ? searchParams.set("ordertype", state.sortordertype)
          : searchParams.delete("ordertype");
      }

      if (state.selected.length) {
        var filtersArray = [];

        state.selected.forEach(function(item) {
          if (item.range) {
            if (state.realDates[item.value]) {
              filtersArray.push(
                "range_" +
                  item.value +
                  "_" +
                  item.key +
                  "^^" +
                  state.realDates[item.value].from +
                  "_" +
                  state.realDates[item.value].to
              );
            } else {
              filtersArray.push(
                "range_" +
                  item.value +
                  "_" +
                  item.key +
                  "^^" +
                  item.range.gte +
                  "_" +
                  item.range.lte
              );
            }
          } else {
            if (item.type && item.type == "query") {
              filtersArray.push("query_" + item.key + "^^" + item.value + "");
            } else {
              filtersArray.push("" + item.key + "^^" + item.value + "");
            }
          }
        });

        var filtersUrl = filtersArray.join("~~");
        searchParams.set("filters", filtersUrl);
      } else {
        searchParams.delete("filters");
      }

      return url;
    }

    function createDates(date) {
      function parseDate(str) {
        var cl = [];
        str.split("-").forEach(function(st) {
          st.length == 1 ? cl.push("0" + st) : cl.push(st);
        });

        return new Date(cl.join("-"));
      }

      function dateDiff(first, second) {
        return Math.round((second - first) / (1000 * 60 * 60 * 24));
      }

      // 2451544 = 2000- 1- 1
      var timestamp = 2451544;
      return dateDiff(parseDate("2000-01-01"), parseDate(date)) + timestamp;
    }
  });
};

function readableDate(date) {
  return (
    date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + date.getDate()
  );
}

var store = new Vuex.Store({
  state: {
    loading: false,
    selected: [],
    sortorder: "desc",
    sortordertype: "score",
    hits: "",
    aggs: "",
    // eslint-disable-next-line no-undef
    size: parseInt(window.mw.config.values.WSSearchFront.config.settings.size),
    total: 0,
    from: 0,
    rangeFrom: 0,
    rangeTo: 0,
    term: "",
    loaded: false,
    dates: [],
    realDates: {},
  },
  mutations: {
    SET_SELECTED(state, selected) {
      state.selected = selected;
    },
    SET_TERM(state, term) {
      state.term = term;
    },
    SET_ORDER(state, order) {
      state.sortorder = order;
    },
    SET_SIZE(state, size) {
      state.size = size;
    },
    SET_ORDERTYPE(state, type) {
      state.sortordertype = type;
    },
    SET_FROM(state, from) {
      state.from = from;
    },
    START(state, start) {
      state.loaded = start;
    },
    SET_RANGE_from(state, range) {
      state.rangeFrom = range;
    },
    SET_RANGE_to(state, range) {
      state.rangeTo = range;
    },
    SET_REAL_DATES(state, date) {
      state.realDates = date;
    },
  },
  getters: {
    rangeFrom(state) {
      return state.rangeFrom;
    },
    rangeTo(state) {
      return state.rangeTo;
    },
  },
  plugins: [updateStore],
});

export default store;
