<template>
  <div class="wssearch--daterange">
    <div class="wssearch--filter-header">
      <label>{{ labelName }}</label>
    </div>
    {{ $i18n("date-range-from") }}
    <div id="dateinputfrom"></div>
    {{ $i18n("date-range-to") }}
    <div id="dateinputto"></div>
    <facet-checbox
      v-if="showCheckbox"
      :agg="agg"
      :index="0"
      :name="'Modification date'"
    ></facet-checbox>
  </div>
</template>

<script>
import FacetCheckbox from "./FacetCheckbox";

export default {
  mounted() {
    this.dateInputs();
  },
  props: {
    label: String,
  },
  components: {
    "facet-checbox": FacetCheckbox,
  },
  computed: {
    labelName() {
      return this.label ? this.label : "Date range";
    },
    showCheckbox() {
      if (this.$store.getters.rangeFrom && this.$store.getters.rangeTo) {
        return true;
      }
      return false;
    },
    agg() {
      return {
        key: "customrange",
        from: this.$store.getters.rangeFrom,
        to: this.$store.getters.rangeTo,
        doc_count: 1,
      };
    },
  },
  methods: {
    dateInputs() {
      var state = this.$store.state;
      var that = this;
      var date = {};
      var dateInput = {};
      var dateInputs = ["from", "to"];

      dateInputs.forEach((element) => {
        // eslint-disable-next-line no-undef
        dateInput[element] = new mw.widgets.DateInputWidget();

        if (state.realDates["customrange"]) {
          dateInput[element].setValue(state.realDates["customrange"][element]);
        }

        dateInput[element].on("change", function () {
          date[element] = dateInput[element].getValue();
          dateInput["to"].mustBeAfter = date["from"];
          dateInput["from"].mustBeBefore = date["to"];

          that.$store.commit(
            "SET_RANGE_" + element,
            that.createDates(date[element]) + 1
          );

          if (state.rangeTo > 0) {
            var selectedKeys = Object.keys(state.selected);

            var realdates_updated = state.realDates;
            realdates_updated["customrange"] = {
              from: date["from"],
              to: date["to"],
            };

            that.$store.commit("SET_REAL_DATES", realdates_updated);

            selectedKeys.forEach(function (el, i) {
              if (state.selected[i].value == "customrange") {
                var te = "";
                if (element == "from") {
                  te = "gte";
                } else {
                  te = "lte";
                }
                var selected_updated = state.selected;

                selected_updated[i].range[te] = Number(
                  state[
                    "range" + element.charAt(0).toUpperCase() + element.slice(1)
                  ] + ".0000000"
                );


                that.$store.commit("SET_SELECTED", selected_updated);
              }

            });
          }
        });
        document
          .querySelector("#dateinput" + element)
          .appendChild(dateInput[element].$element[0]);
      });
    },
    createDates(date) {
      function parseDate(str) {
        var cl = [];
        str.split("-").forEach(function (st) {
          st.length == 1 ? cl.push("0" + st) : cl.push(st);
        });
        return new Date(cl.join("-"));
      }

      function datediff(first, second) {
        return Math.round((second - first) / (1000 * 60 * 60 * 24));
      }

      // 2451544 = 2000- 1- 1
      var timestamp = 2451544;
      return datediff(parseDate("2000-01-01"), parseDate(date)) + timestamp;
    },
  },
};
</script>

<style>
.wssearch--daterange .wssearch--checkbox-count,
.wssearch--daterange .wssearch--checkbox-title {
  display: none;
}
.wssearch--daterange .wssearch--checkbox {
  display: block;
}
.wssearch--daterange .wssearch--checkbox-input {
  position: relative;
  display: block;
}
.wssearch--daterange .wssearch--checkbox-input-icon {
  width: 100%;
}
</style>
