<template>
  <div
    aria-disabled="false"
    :class="
      'wssearch--filter__' + name.toLowerCase().replace(/[^a-z0-9]/gim, '_')
    "
  >
    <div class="wssearch--filter-header">
      <label>{{ labelName }}</label>
    </div>
    <div class="wssearch--search-wrapper">
      <input
        type="text"
        :placeholder="$i18n('search')"
        class="wssearch--facetsearch-input"
        @keyup.enter="search"
        v-model="termTyped"
        tabindex="0"
        aria-disabled="false"
      />
      <span class="wssearch--search-icon"> </span>
      <span
        v-if="termTyped"
        class="wssearch--search-clear"
        tabindex="-1"
        aria-label="Clear"
        role="button"
        @click="clearTerm"
      >
      </span>
    </div>
    <span
      v-if="false"
      class="wssearch--search-button"
      aria-label="Search"
      @click="search"
      role="button"
      tabindex="1"
      aria-disabled="false"
    >
      {{ $i18n("search") }}
    </span>
    <input
      type="checkbox"
      :value="val"
      :id="setID"
      @change="clearTerm"
      style="display: none"
    />
  </div>
</template>

<script>
export default {
  computed: {
    labelName() {
      return this.label ? this.label : this.name;
    },
    val() {
      return { key: this.name, value: this.termTyped, type: "query" };
    },
    setID() {
      return (
        this.name.toLowerCase().replace(/[^a-z0-9]/gim, "_") +
        "--" +
        this.termTyped.toLowerCase().replace(/[^a-z0-9]/gim, "_")
      );
    },
    term: {
      set(value) {
        this.termClear = value;
        this.userHasTyped = true;
      },
      get() {
        if (this.termClear || this.userHasTyped) {
          return this.termClear;
        }
        return this.$store.state.term;
      },
    },
  },
  props: {
    name: String,
    label: String,
  },
  data() {
    return {
      termClear: "",
      userHasTyped: false,
      termTyped: "",
    };
  },
  methods: {
    search() {
      var that = this;
      var val = this.$store.state.selected;
      var newSelected = val.filter(function (el) {
        if (el.key != that.name) {
          return el;
        }
      });
      if (this.termTyped) {
        newSelected.push({ key: this.name, value: this.termTyped, type: "query" });
      }
      this.$store.commit("SET_SELECTED", newSelected);
    },
    clearTerm() {
      this.termTyped = "";
      var that = this;
      var val = this.$store.state.selected;
      var newSelected = val.filter(function (el) {
        if (el.key != that.name) {
          return el;
        }
      });
      this.$store.commit("SET_SELECTED", newSelected);
    },
  },
};
</script>

<style>
.wssearch--facetsearch-input {
  box-sizing: border-box;
  cursor: text;
  box-shadow: inset 0 0 0 1px transparent;
  -webkit-transition: border-color 250ms, box-shadow 250ms;
  -moz-transition: border-color 250ms, box-shadow 250ms;
  transition: border-color 250ms, box-shadow 250ms;
  background-color: #fff;
  color: #000;
  margin: 0;
  border: 1px solid #a2a9b1;
  border-radius: 2px;
  padding: 0.4em 0.6em 0.4em 2.2em;
  font-size: inherit;
  font-family: inherit;
  line-height: 1.42857143em;
  width: 100%;
}

.wssearch--facetsearch-input:hover {
  border-color: #72777d;
}
.wssearch--facetsearch-input:focus {
  outline: 0;
  border-color: var(--base-color);
  box-shadow: inset 0 0 0 1px var(--base-color);
}
</style>